export default "import React from 'react';\r\nimport {\r\n  Area,\r\n  AreaChart,\r\n  Brush,\r\n  CartesianGrid,\r\n  Line,\r\n  LineChart,\r\n  ResponsiveContainer,\r\n  Tooltip,\r\n  XAxis,\r\n  YAxis,\r\n} from 'recharts';\r\nimport data from './data';\r\nimport Box from '@mui/material/Box';\r\n\r\nconst SynchronizedLineChart = () => (\r\n  <ResponsiveContainer width='100%' height={700}>\r\n    <Box>\r\n      <Box textAlign='center' component='p'>\r\n        A demo of synchronized AreaCharts\r\n      </Box>\r\n      <Box mb={4}>\r\n        <ResponsiveContainer width='100%' height={200}>\r\n          <LineChart\r\n            data={data}\r\n            syncId='anyId'\r\n            margin={{top: 10, right: 0, left: -25, bottom: 0}}\r\n          >\r\n            <XAxis dataKey='name' />\r\n            <YAxis />\r\n            <CartesianGrid strokeDasharray='3 3' />\r\n            <Tooltip />\r\n            <Line\r\n              type='monotone'\r\n              dataKey='uv'\r\n              stroke='#4299E1'\r\n              fill='#4299E1'\r\n            />\r\n          </LineChart>\r\n        </ResponsiveContainer>\r\n      </Box>\r\n      <Box textAlign='center' component='p'>\r\n        Maybe some other content\r\n      </Box>\r\n      <Box mb={4}>\r\n        <ResponsiveContainer width='100%' height={200}>\r\n          <LineChart\r\n            data={data}\r\n            syncId='anyId'\r\n            margin={{top: 10, right: 0, left: -25, bottom: 0}}\r\n          >\r\n            <XAxis dataKey='name' />\r\n            <YAxis />\r\n            <CartesianGrid strokeDasharray='3 3' />\r\n            <Tooltip />\r\n            <Line\r\n              type='monotone'\r\n              dataKey='pv'\r\n              stroke='#4299E1'\r\n              fill='#4299E1'\r\n            />\r\n          </LineChart>\r\n        </ResponsiveContainer>\r\n      </Box>\r\n      <Box mb={4}>\r\n        <ResponsiveContainer width='100%' height={200}>\r\n          <AreaChart\r\n            data={data}\r\n            syncId='anyId'\r\n            margin={{top: 10, right: 0, left: -25, bottom: 0}}\r\n          >\r\n            <XAxis dataKey='name' />\r\n            <YAxis />\r\n            <CartesianGrid strokeDasharray='3 3' />\r\n            <Tooltip />\r\n            <Area\r\n              type='monotone'\r\n              dataKey='pv'\r\n              stroke='#4299E1'\r\n              fill='#4299E1'\r\n            />\r\n            <Brush />\r\n          </AreaChart>\r\n        </ResponsiveContainer>\r\n      </Box>\r\n    </Box>\r\n  </ResponsiveContainer>\r\n);\r\n\r\nexport default SynchronizedLineChart;\r\n";