export default "import React from 'react';\r\nimport {store} from 'react-notifications-component';\r\nimport Button from '@mui/material/Button';\r\n\r\nimport notification from '../helpers/notification';\r\nimport {getMessage, getTitle, getType} from '../helpers/randomize';\r\nimport Box from '@mui/material/Box';\r\n\r\nexport default class ContainerExample extends React.Component {\r\n  add = (container) => {\r\n    const type = getType();\r\n\r\n    return store.addNotification(\r\n      Object.assign({}, notification, {\r\n        title: getTitle(type),\r\n        message: getMessage(type),\r\n        container,\r\n        type,\r\n      }),\r\n    );\r\n  };\r\n\r\n  render() {\r\n    return (\r\n      <Box display='flex' flexWrap='wrap'>\r\n        <Box mr={2} my={1}>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('top-left')}\r\n          >\r\n            Top Left\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1}>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('top-right')}\r\n          >\r\n            Top Right\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1}>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('top-center')}\r\n          >\r\n            Top Center\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1}>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('bottom-left')}\r\n          >\r\n            Bottom Left\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1}>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('bottom-right')}\r\n          >\r\n            Bottom Right\r\n          </Button>\r\n        </Box>\r\n        <Box mr={2} my={1}>\r\n          <Button\r\n            color='primary'\r\n            variant='contained'\r\n            onClick={() => this.add('bottom-center')}\r\n          >\r\n            Bottom Center\r\n          </Button>\r\n        </Box>\r\n      </Box>\r\n    );\r\n  }\r\n}\r\n";