export default "import React from 'react';\r\nimport {\r\n  Area,\r\n  AreaChart,\r\n  CartesianGrid,\r\n  ResponsiveContainer,\r\n  Tooltip,\r\n  XAxis,\r\n  YAxis,\r\n} from 'recharts';\r\nimport {useThemeContext} from '../../../../../@crema/utility/AppContextProvider/ThemeContextProvider';\r\n\r\nconst data = [\r\n  {month: '2015.01', a: 4000, b: 2400, c: 2400},\r\n  {month: '2015.02', a: 3000, b: 1398, c: 2210},\r\n  {month: '2015.03', a: 2000, b: 9800, c: 2290},\r\n  {month: '2015.04', a: 2780, b: 3908, c: 2000},\r\n  {month: '2015.05', a: 1890, b: 4800, c: 2181},\r\n  {month: '2015.06', a: 2390, b: 3800, c: 2500},\r\n  {month: '2015.07', a: 3490, b: 4300, c: 2100},\r\n];\r\n\r\nconst getPercent = (value, total) => {\r\n  const ratio = total > 0 ? value / total : 0;\r\n\r\n  return toPercent(ratio, 2);\r\n};\r\n\r\nconst toPercent = (decimal, fixed = 0) => {\r\n  return `${(decimal * 100).toFixed(fixed)}%`;\r\n};\r\nconst renderTooltipContent = (o) => {\r\n  const {payload, label} = o;\r\n  const total = payload.reduce((result, entry) => result + entry.value, 0);\r\n\r\n  return (\r\n    <div className='customized-tooltip-content'>\r\n      <p className='total'>{`${label} (Total: ${total})`}</p>\r\n      <ul className='list'>\r\n        {payload.map((entry, index) => (\r\n          <li key={`item-${index}`} style={{color: entry.color}}>\r\n            {`${entry.name}: ${entry.value}(${getPercent(entry.value, total)})`}\r\n          </li>\r\n        ))}\r\n      </ul>\r\n    </div>\r\n  );\r\n};\r\nconst PercentAreaChart = () => {\r\n  const {theme} = useThemeContext();\r\n  return (\r\n    <ResponsiveContainer width='100%' height={200}>\r\n      <AreaChart\r\n        data={data}\r\n        stackOffset='expand'\r\n        margin={{top: 10, right: 0, left: -25, bottom: 0}}\r\n      >\r\n        <XAxis dataKey='month' />\r\n        <YAxis tickFormatter={toPercent} />\r\n        <CartesianGrid strokeDasharray='3 3' />\r\n        <Tooltip content={renderTooltipContent} />\r\n        <Area\r\n          type='monotone'\r\n          dataKey='a'\r\n          stackId='1'\r\n          fill={theme.palette.primary.main}\r\n        />\r\n        <Area\r\n          type='monotone'\r\n          dataKey='b'\r\n          stackId='1'\r\n          stroke={theme.palette.secondary.main}\r\n          fill={theme.palette.secondary.main}\r\n        />\r\n        <Area\r\n          type='monotone'\r\n          dataKey='c'\r\n          stackId='1'\r\n          stroke={theme.palette.success.main}\r\n          fill={theme.palette.success.main}\r\n        />\r\n      </AreaChart>\r\n    </ResponsiveContainer>\r\n  );\r\n};\r\n\r\nexport default PercentAreaChart;\r\n";