export default "import React from 'react';\r\nimport {\r\n  Area,\r\n  AreaChart,\r\n  CartesianGrid,\r\n  ResponsiveContainer,\r\n  Tooltip,\r\n  XAxis,\r\n  YAxis,\r\n} from 'recharts';\r\nimport Box from '@mui/material/Box';\r\nimport {useThemeContext} from '../../../../../@crema/utility/AppContextProvider/ThemeContextProvider';\r\n\r\nconst data = [\r\n  {name: 'Page A', uv: 4000},\r\n  {name: 'Page B', uv: 3000},\r\n  {name: 'Page C', uv: 2000},\r\n  {name: 'Page D'},\r\n  {name: 'Page E', uv: 1890},\r\n  {name: 'Page F', uv: 2390},\r\n  {name: 'Page G', uv: 3490},\r\n];\r\nconst AreaChartConnectNulls = () => {\r\n  const {theme} = useThemeContext();\r\n  return (\r\n    <ResponsiveContainer width='100%'>\r\n      <div>\r\n        <Box mb={5}>\r\n          <ResponsiveContainer width='100%' height={200}>\r\n            <AreaChart\r\n              data={data}\r\n              margin={{top: 10, right: 0, left: -25, bottom: 0}}\r\n            >\r\n              <XAxis dataKey='name' />\r\n              <YAxis />\r\n              <CartesianGrid strokeDasharray='3 3' />\r\n              <Tooltip />\r\n              <Area\r\n                type='monotone'\r\n                dataKey='uv'\r\n                fill={theme.palette.primary.main}\r\n              />\r\n            </AreaChart>\r\n          </ResponsiveContainer>\r\n        </Box>\r\n        <Box mb={4}>\r\n          <ResponsiveContainer width='100%' height={200}>\r\n            <AreaChart\r\n              data={data}\r\n              margin={{top: 10, right: 0, left: -25, bottom: 0}}\r\n            >\r\n              <XAxis dataKey='name' />\r\n              <YAxis />\r\n              <CartesianGrid strokeDasharray='3 3' />\r\n              <Tooltip />\r\n              <Area\r\n                connectNulls={true}\r\n                type='monotone'\r\n                dataKey='uv'\r\n                stroke={theme.palette.secondary.main}\r\n                fill={theme.palette.secondary.main}\r\n              />\r\n            </AreaChart>\r\n          </ResponsiveContainer>\r\n        </Box>\r\n      </div>\r\n    </ResponsiveContainer>\r\n  );\r\n};\r\n\r\nexport default AreaChartConnectNulls;\r\n";